
.break {
  position: relative;
  overflow: hidden;
}

.break__bg {
  position: absolute;
  top: -200px;
  bottom: -200px;
  left: 0;
  right: 0;
  background-image: url("/assets/images/bg-base.jpg");
  background-repeat: no-repeat;
  background-size: cover;
  background-position: center;
  z-index: -1;
}

.break__text {
  color: #fff;
  text-align: center;
  font-weight: 400;
}